body.--js-scroll-hidden {
  overflow: hidden;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(00, 00, 11, 0.5);

  opacity: 0;
  visibility: hidden;

  transition: all 0.3s ease 0s;

  &.--js-show {
    opacity: 1;
    visibility: visible;

    .modal-body {
      transform: translate(0);
      opacity: 1;
    }
  }
}

.modal-body {
  position: relative;
  min-width: 200px;
  padding: 30px 10px;
  background: #ffffff;

  max-height: 100vh;


  overflow-y: auto;
  overflow-x: hidden;

  transition: all 0.3s ease 0s;
  opacity: 0;
  transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  line-height: 0;
  text-decoration: none;
}